import { Injectable } from '@angular/core';
import { Plugin, Cordova, IonicNativePlugin } from '@ionic-native/core';

export interface LineLoginResponse {
  userID: number;
  displayName: string;
  pictureURL: string;
}

/**
 * @name LineLogin
 * @description
 */
@Plugin({
  pluginName: 'LineLogin',
  plugin: 'cordova-plugin-line-login',
  pluginRef: 'lineLogin', // the variable reference to call the plugin, example: navigator.geolocation
  repo: 'https://github.com/kntmrkm/cordova-line-login-plugin', // the github repository URL for the plugin
  install: 'ionic cordova plugin add https://github.com/kntmrkm/cordova-line-login-plugin --variable LINE_CHANNEL_ID={your_line_channel_id}',
  installVariables: ['LINE_CHANNEL_ID'],
  platforms: ['Android', 'iOS']
})
@Injectable()
export class LineLogin extends IonicNativePlugin {

  /**
   * This function does something
   * @param arg1 {string} Some param to configure something
   * @param arg2 {number} Another param to configure something
   * @return {Promise<any>} Returns a promise that resolves when something happens
   */
  @Cordova()
  initialize(param: {}): Promise<any> { return; }

  @Cordova()
  login(param: {}): Promise<LineLoginResponse> { return; }
}
